/* Make sure we are 100% height all the way to our frame (for flex layout) */
html,
body,
#root,
.frame {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  /* Background color for the unused portion of our screen
   * (Likely computers - not visible on mobile devices)
   * The screen must be wider than .frame/max-width for this
   * to be visible
   */
  background-color: lightgray;
}

/* Colors for the two teams */
.minority {
  color: darkgreen;
  fill: darkgreen;
}

.majority {
  color: darkblue;
  fill: darkblue;
}

/* Top-level screen layout */
.frame {
  width: 100%;
  max-width: 600px;

  /* Flex layout to support fixed header/footer with scrolling interior */
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;

  /* Centers the frame when max-width is in-use */
  margin: auto;

  > header {
    /* Fixed layout - neither shrink nor grow */
    flex-shrink: 0;

    background-color: black;
    color: white;

    > h1 {
      margin-top: 0px;
      margin-bottom: 0px;

      &.left {
        text-align: left;
        display: inline-block;
      }

      &.right {
        text-align: right;
        float: right;
      }
    }

    > .player {
      text-align: right;
    }

    > .score {
      text-align: right;

      svg {
        height: 16px;
        width: 16px;
        margin-left: 3px;
        margin-right: 3px;
        vertical-align: text-bottom;
        padding-bottom: 2px;
      }

      svg.majority {
        padding-bottom: 3px;
      }
    }
  }

  > #content {
    /* This is the flexible layout element.
     * 1. Don't let it shrink below a minimum size
     * 2. Apply scrollbars if it is unable to fully fit
     */
    flex-grow: 1;
    min-height: 100px;
    overflow: auto;

    /* Border on the left and right only. We are sandwiched between the 
     * header and footer.
     */
    border-width: 0 1px 0 1px;
    border-style: solid;
    border-color: black;
    box-sizing: border-box;

    /* A small buffer around the actual content inside the frame.
     * NOTE: FF intentionally does not correctly render the bottom padding when scrolling
     */
    padding: 5px;

    background-color: white;
    color: black;

    .heading {
      border-bottom: 1px solid black;
      margin-bottom: 10px;

      .left {
        text-align: left;
        display: inline;
      }

      .right {
        text-align: right;
        float: right;
      }

      /* Styles to make a button look like a link */
      .linkButton {
        background-color: transparent;
        border: none;
        cursor: pointer;
        text-decoration: underline;
        display: inline;
        margin: 0;
        padding: 0;

        :hover,
        :focus {
          text-decoration: none;
        }
      }
    }
  }

  > footer {
    /* Fixed layout - neither shrink nor grow */
    flex-shrink: 0;

    background-color: white;

    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    box-sizing: border-box;
  }
}

/* iconBar - used in footer to control which view is displayed to the user */
.iconBar {
  width: 100%;

  background-color: darkgray;

  > div {
    /* Lay out all DIVs on the same line */
    display: inline-block;
    zoom: 1; /* Trigger hasLayout */

    /* Center content within each div */
    text-align: center;

    border: 1px solid black;
    box-sizing: border-box;

    > svg {
      /* To center SVG elements vertically */
      padding-top: 2px;

      /* Affects size of entire iconBar */
      height: 24px;
      width: 24px;
    }
  }

  > #selected {
    background-color: black;
    fill: white;
  }
}

/* Used when the panel is selected */
.playerPicker {
  margin-top: 15px;
  font-size: 20pt;
  margin-left: 20px;

  svg {
    height: 24px;
    width: 24px;
    fill: green;
    padding-right: 4px;
    vertical-align: text-bottom;
    padding-bottom: 4px;
  }
}

table {
  thead,
  tfoot {
    background-color: black;
    color: white;
  }

  tfoot {
    font-size: 2px;
  }

  tbody {
    tr:nth-child(odd) {
      background-color: lightgray;
    }
  }
}

/* Cards used on the GameLog screen */
.historyEntry {
  border: 1px solid black;
  margin-top: 15px;
  background-color: lightgray;

  div:first-of-type {
    background-color: black;
    color: white;
  }

  table {
    margin: 10px 0px 10px 0px;
    border: 1px solid black;

    td:nth-child(2) {
      text-align: center;

      .yes {
        height: 16px;
        width: 16px;
        fill: green;
      }

      .no {
        height: 20px;
        width: 20px;
        fill: red;
      }
    }
  }
}

/* Used on the game log screen after the game as completed */
.token {
  svg {
    height: 12px;
    width: 12px;
    margin-right: 3px;
  }

  svg.majority {
    padding-bottom: 3px;
  }
}

/* Form layout - used on create/join pages */
form {
  display: grid;
  grid-template-columns: 75px 120px 70px;
  grid-column-gap: 10px;
  grid-row-gap: 5px;

  label {
    grid-column: 1 / 2;
    text-align: right;
  }

  input {
    grid-column: 2 / 4;
  }

  button {
    grid-column: 3 / 4;
    text-align: center;
    margin-top: 10px;
    max-width: 70px;
    float: right;
  }
}

/* Action bar - used for buttons and waiting messages */
.actionBar {
  text-align: center;
  background-color: lightyellow;
  border: 1px solid yellow;
  margin: 30px 50px 0 50px;
  padding: 5px 0 5px 0;

  button {
    margin: 0 5px 0 5px;
  }
}

/* Message Bar - used for user notifications */
.messageBar {
  margin: 5px 10px 5px 10px;
  padding: 2px 5px 2px 5px;

  span {
    display: inline;
  }

  svg {
    height: 18px;
    float: right;
  }

  &#error {
    border: 1px solid darkred;
    background-color: red;
    fill: darkred;
  }

  &#message {
    border: 1px solid darkgreen;
    background-color: green;
    fill: darkgreen;
  }
}

/* Share Control - used on Game Lobby */
.shareControl {
  margin-top: 20px;
  text-align: center;

  div {
    margin-bottom: 10px;
  }

  svg {
    vertical-align: middle;
    padding-bottom: 2px;
    height: 16px;
    fill: black;
  }
}

/* Team Banner - used on Team panel and Game Completed screen */
.teamBanner {
  text-align: center;

  span {
    font-size: 36pt;
    vertical-align: bottom;
  }

  svg {
    height: 64px;
    width: 64px;
  }
}

.tips {
  border: 1px solid darkblue;
  background-color: lightblue;
  margin: 20px 50px 0 50px;

  svg {
    height: 12px;
    width: 12px;
  }
}

.help {
  svg {
    vertical-align: middle;
    padding-bottom: 2px;
    height: 16px;
    width: 16px;
  }
}
